/* General reset */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: white;
  }
  body {
    background-image: url("https://media.cungphuot.info/2017/10/homestay-da-lat-ivivu-1.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
  }
  
  body::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Màu nền đen mờ */
    z-index: -1;
  }
  
  header {
    background-color: #2c3e50;
    color: white;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  header h1 {
    margin: 0;
  }
  
  header .chutrang {
    color: #e74c3c;
  }
  
  header nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  header nav ul li {
    margin-left: 15px;
  }
  
  header nav ul li img {
    border-radius: 50%;
  }
  
  h3 {
    text-align: center;
    color: #34495e;
    margin: 20px 0;
  }
  
  .dang_nhap,
  .dang_nhap1 {
    max-width: 400px;
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #bdc3c7;
    border-radius: 8px;
    background-color: #ecf0f1;
    text-align: center;
  }
  
  .dang_nhap input[type="email"] {
    width: 90%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #bdc3c7;
    border-radius: 4px;
  }
  
  .dang_nhap input[type="submit"] {
    background-color: #3498db;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .dang_nhap input[type="submit"]:hover {
    background-color: #2980b9;
  }
  
  .dang_nhap1 button {
    background-color: #2ecc71;
    color: white;
    padding: 10px 15px;
    margin: 5px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .dang_nhap1 button:hover {
    background-color: #27ae60;
  }
  .hidden {
    display: none;
  }
  
  footer {
    background-color: #34495e;
    color: white;
    padding: 30px 0;
  }
  
  footer #ket {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  footer .noidung {
    flex: 1;
    margin: 10px;
    min-width: 250px;
  }
  
  footer .logo img {
    max-width: 100%;
    height: auto;
  }
  
  footer h3 {
    margin-top: 0;
    color: #f1c40f;
  }
  
  footer .email input {
    width: 70%;
    padding: 8px;
    margin-right: 5px;
    border: none;
    border-radius: 4px;
  }
  
  footer .email button {
    background-color: #e67e22;
    color: white;
    border: none;
    padding: 8px 15px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  footer .email button:hover {
    background-color: #d35400;
  }
  